import {ListGroup, ListGroupItem} from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';

const Sidebar = props => <ListGroup fill>
  <LinkContainer to='/dashboard'><ListGroupItem></ListGroupItem></LinkContainer>
  <LinkContainer to='/schedule'><ListGroupItem></ListGroupItem></LinkContainer>
  <LinkContainer to='/teacherslist'><ListGroupItem> </ListGroupItem></LinkContainer>
  <LinkContainer to='/payment'><ListGroupItem></ListGroupItem></LinkContainer>
  <LinkContainer to='/archive'><ListGroupItem></ListGroupItem></LinkContainer>
  <LinkContainer to='/tests'><ListGroupItem></ListGroupItem></LinkContainer>
  <LinkContainer to='/help'><ListGroupItem></ListGroupItem></LinkContainer>
  <LinkContainer to='/chat'><ListGroupItem></ListGroupItem></LinkContainer>
</ListGroup>;

export default Sidebar;



